import { useSelector } from "react-redux";
import styled from "styled-components";
import MessegForm from "./MassegeForm/MassegeForm";


const MessegesContainer = (props)=>{

  const dialogMesseges = useSelector(state=>state.dialog.activeUserDialog)
 
  const addNewMessage = (values) => {
    props.sendNewMeseg(values.newMessageBody);
}
  

  return (
    <>
      {dialogMesseges.length?<MessegForm onSubmit={addNewMessage}/>: null}
      {props.dial}
    </>
  )
};

const S = {
  Wrapper: styled.div`
   & a {
     
   }
  `
}

export default MessegesContainer;