import { connect, useSelector } from "react-redux";
import styled from "styled-components";
import Messeges from "./UserDialogItems/Masseges/Messeges";
import UserDialog from "./UserDialogItems/UserDialogItems";
import { getDialog, deleteUserDial, sendNewMeseg} from "../../ReduxStore/reduser/dialogReduser/dialogReduser";
import { useEffect} from "react";
import MessegesContainer from "./UserDialogItems/Masseges/MesegesContainer";

const Dialogs =(props)=>{


  const dialog = useSelector(state=> state.dialog);
  const dialogMesseges = useSelector(state=>state.dialog.activeUserDialog)

  const dialogsElements = dialog.dialogs.map(dial=>(<UserDialog 
    name={dial.name}
    userId={dial.userId}
    key={dial.userId}
    getDialog={props.getDialog}
    dialogMesseges={dialogMesseges}
  />))

  let dial
  
  if (dialogMesseges.length) {
    dial = dialogMesseges[0].masseges.map(mes=>{
      return (<Messeges key={mes.id} messeg={mes.massege}/>)})
  }
  
  useEffect(()=>{
    return props.deleteUserDial();
  }, [])
  

  return(
    <S.Dialogs>
      <div className='dialog-user'>
        <div>{dialogsElements}</div>
      </div>
      <div className='dialog-massege'>
        <MessegesContainer sendNewMeseg={props.sendNewMeseg} dial={dial}/>
      </div>
    </S.Dialogs>
  )
};

const S ={
  Dialogs: styled.div`
  background-color: #fff;
  width:100%;
  display:flex;
  justify-content: center;
  @media screen and (max-width: 650px){
    display: flex;
    flex-direction: column;
    height: 100%;
  }
  .dialog-user,
  .dialog-massege {
    border-radius: 30px;
    margin: 10px;
    padding: 10px 0 0 0;
    > form {
      display: flex;
      flex-direction: column;
      align-items: center;
      > div {
        margin-top: 10px;
        width:95%;
        > button {
          text-decoration: none;
          display: inline-block;
          padding: 6px 50px;
          margin: 20px 10px;
          border-radius: 10px;
          box-shadow: 0 0 40px 40px #F137A6 inset, 0 0 0 0 #F137A6;
          font-family: 'Montserrat', sans-serif;
          font-weight: bold;
          letter-spacing: 2px;
          color: white;
          transition: .15s ease-in-out;
        }
        > button:hover {
          box-shadow: 0 0 10px 0 #F137A6 inset, 0 0 10px 4px #F137A6;
          color: #F137A6;
        }
        > div {
          width: 90%; 
          >div {
            width: 100%; 
            > textarea {
            width: 100%;
            resize: none;
            height: 100px;
            border-radius: 20px;
            outline: none;
            padding: 10px;
          }
          > textarea::placeholder {
            text-align: center;
            padding-top: 20px;
          }
          }
          
        }
      }
    }
    & div {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
  .dialog-user {
    width: 40%;
    background-color: #C9FCFF;
    @media screen and (max-width: 650px){
    width: 90%;
    padding: 7px;
    > div {
      display:flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-around;
      > div {
        margin: 5px;
      }
    }
  }

  }
  .dialog-massege {
    width: 50%;
    background-color: #C9FCFF;
    @media screen and (max-width: 650px){
    width: 98%;
    height: 50vh;
  }
  } 
  `
}

export default connect(null, {getDialog, deleteUserDial, sendNewMeseg})(Dialogs);