const dialogReduser=(state = defaultState, action)=>{
  switch (action.type) {
    case DELETE_DIALOG:
      return {
          ...state,
          activeUserDialog: []
      };
    case SEND_MESSAGE: 
      return {
        ...state,
        activeUserDialog:[{...state.activeUserDialog[0], masseges: [...state.activeUserDialog[0].masseges, {id:(state.activeUserDialog[0].masseges.length+1), massege: action.newMessage}]}]
      };
    case UPDATE_DIALOG:
      return {
        ...state,
        dialogs: [...state.dialogs.map((actualDi)=> {
          if (actualDi.userId !== action.actualDial.userId) {
            return actualDi
          } else{
            return action.actualDial
          }
        })]
      }
      case GET_DIAlOG:
        return {
          ...state,
          activeUserDialog: state.dialogs.filter(dial=>{
             if(dial.userId===action.userId){
               return dial
             }
          })
        };
    default:
      return state;
  }
};

export const getDialog = (userId, actualD=null)=>(dispatch)=>{
  if (actualD) {
    
    return (dispatch(updateDialog(actualD)), dispatch(getUserDialog(userId)));
  }
  return dispatch(getUserDialog(userId));
};
export const deleteUserDial = ()=>(dispatch)=>{
  return dispatch(deleteUserDialog());
};
export const sendNewMeseg = (newMessage)=>(dispatch)=>{
  return dispatch(sendMeseges(newMessage));
};
