import { GET_DIAlOG, DELETE_DIALOG, SEND_MESSAGE, UPDATE_DIALOG } from "../constans/constans";

export const getUserDialog = (userId) =>({type: GET_DIAlOG, userId});
export const deleteUserDialog = () =>({type: DELETE_DIALOG});
export const sendMeseges = (newMessage)=>({type: SEND_MESSAGE, newMessage});
export const updateDialog = (actualDial) => ({type: UPDATE_DIALOG,actualDial})