import { Field, reduxForm } from "redux-form";
import { Textarea } from "../../../../../common/FormControl/FormControl";
import { maxLengthCreator, required } from "../../../../../utilit/validators/validators";

const maxLength50 = maxLengthCreator(50);

const MessegForm =(props)=>{
  return (
    <form onSubmit={props.handleSubmit}>
            <div>
                <Field component={Textarea}
                       validate={[required, maxLength50]}
                       placeholder='Enter your message' name="newMessageBody" />
            </div>
            <div>
                <button>Send</button>
            </div>
    </form>
  )
};

export default reduxForm({form: 'dialog-add-message-form'})(MessegForm)